<template>
  <div class="write" ref="inputs" :style="{height:writeHeight}">
    <div class="inputs">
      <textarea-autosize
        class="inputText title"
        placeholder="यहाँ शिर्षक लेख ..."
        rows="1"
        :max-height="80"
        @input="debounceTitleInput"
        v-model="title"
      />
      <textarea
        class="inputText text"
        ref="textInput"
        placeholder="यहाँ कुराहरु लेख अनि तलको पहेलो बटन थिच ..."
        :style="{height:textInputHeight}"
        v-model="text"
      ></textarea>
      <button v-if="spaceForButton">लेख्नुहोस</button>
    </div>
  </div>
</template>

<script>
import debounce from "debounce";
export default {
  created() {
    window.addEventListener("resize", this.debouncedResizeWatch);
  },
  beforeDestroy() {
    window.removeEventListener("resize", this.debouncedResizeWatch);
  },
  data() {
    return {
      windowHeight: 0,
      yOfTextInput: 0,
      title: "",
      text: ""
    };
  },
  methods: {
    debounceTitleInput() {
      debounce(() => {
        this.yOfTextInput = this.$refs.textInput.getBoundingClientRect().top;
      }, 200)();
    },
    debouncedResizeWatch() {
      debounce(() => {
        this.windowHeight = window.innerHeight;
      })();
    }
  },
  mounted: function() {
    this.windowHeight = window.innerHeight;
    this.$nextTick(() => {
      this.yOfTextInput = this.$refs.textInput.getBoundingClientRect().top;
    });
  },
  computed: {
    writeHeight() {
      var wdH = this.windowHeight; // Not having this line breaks the whole display idk why (of course this.windowHeight is used below in that case)
      if (this.$refs.inputs != undefined) {
        return (
          parseInt(wdH - this.$refs.inputs.getBoundingClientRect().top) + "px"
        );
      } else return 0;
    },
    textInputHeight() {
      var wdH = this.windowHeight;
      let offset = 10;
      if (this.$refs.textInput != undefined) {
        if (this.spaceForButton) offset = 90;
        let height = wdH - this.yOfTextInput - offset;
        if (height > 0)
          return (
            parseInt(
              // wdH - this.$refs.textInput.getBoundingClientRect().top - offset
              wdH - this.yOfTextInput - offset
            ) + "px"
          );
        else return offset;
      } else return 0;
    },
    spaceForButton() {
      if (this.windowHeight > 500) return true;
      return false;
    }
  }
};
</script>

